import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { take } from 'rxjs/operators';
import { ForgotPassword } from '../../@store/auth/auth.actions';
import { BasePage } from '../../base/base.page';
import { NavigationExtrasService } from '../../@core/navigation-extras.service';
import { Constants } from '../../@global/constants';
import { UtilsService } from '../../@core/utils.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.page.html',
  styleUrls: ['./forgot-password.page.scss'],
})
export class ForgotPasswordPage extends BasePage implements OnInit {
  public formGroup: FormGroup;
  private username: string;

  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    private formBuilder: FormBuilder,
    private navigationExtrasService: NavigationExtrasService
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {
    this.username = this.navigationExtrasService.getExtras()?.username;
    this.initForm();
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      email: [
        this.username || '',
        [Validators.required, Validators.email, Validators.maxLength(Constants.MAX_LENGTH_USERNAME_COGNITO)],
      ],
    });
  }

  public onForgotPassword(): void {
    if (this.utilsService.isValidForm(this.formGroup, 'formValidations.checkEmail')) {
      this.store
        .dispatch(
          new ForgotPassword({
            username: this.formGroup.get('email').value,
          })
        )
        .pipe(take(1))
        .subscribe(
          () => {
            this.navigationExtrasService.setExtras({ username: this.formGroup.get('email').value });
            this.router.navigate(['/forgot-password-submit']);
          },
          (error) => {
            console.log(error);
          }
        );
    }
  }
}
